import "./About.css";

const About = () => {
  return (
    <section className="about" id="about">
      <div className="main">
        <div className="about-header">Welcome to Pokdex!</div>
        <div className="about-text-container">
          <div className="about-text">
            <p>
              In this Pokedex collection you can explore a list of 40 Pokmons
              and delve into their fascinating details. Whether you're a
              seasoned Pokmon Trainer or just starting your journey, this
              Pokedex offers a comprehensive look into the world of Pokmon
            </p>
          </div>
        </div>
      </div>
    </section>
  );
};
export default About;
